<template>
  <Modal
    :active="showModal"
    :event-name="'research-status-modal'"
    :custom-width="'w-full ipad:w-[768px] ipadPro:w-[1000px]'"
    :full-screen="'nexus'"
    :custom-padding="'p-spacing-0'"
  >
    <ResearchStatusForm
      :company-data="companyData"
      :market-research-id="marketResearchId"
      :product-id="productId"
      :market-research-settings="marketResearchSettings"
      :status-options="statusOptions"
    />
  </Modal>
</template>

<script>
import Modal from "@/components/General/Modal.vue";
export default {
  components: {
    Modal,
    ResearchStatusForm: () =>
      import("@/components/MarketResearch/ResearchStatusForm.vue"),
  },
  data() {
    return {
      showModal: false,
      companyData: null,
      marketResearchId: null,
      productId: null,
      marketResearchSettings: null,
      statusOptions: [],
    };
  },
  mounted() {
    this.$bus.$on("research-status-modal", (data) => {
      if (data?.showModal) {
        this.showModal = data?.showModal;
        this.companyData = data?.companyData;
        this.marketResearchId = data?.marketResearchId;
        this.marketResearchSettings = data?.marketResearchSettings;
        this.statusOptions = data?.statusOptions;
        if (data?.productId) {
          this.productId = data?.productId;
        }
      } else {
        this.showModal = false;
        this.companyData = null;
        this.marketResearchId = null;
        this.productId = null;
        this.marketResearchSettings = null;
        this.statusOptions = [];
      }
    });
  },
  beforeDestroy() {
    this.$bus.$off("research-status-modal");
  },
};
</script>
